* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App{
    width: 800px;
}

.post{
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}
.post__btn{
  display: flex;
}

.post-enter {
    transform: translateX(-350px); 
  }
  .post-enter-active {
    transform: translateX(0px);
    transition: all 500ms ease-in;
  }
  .post-exit {
    opacity: 1;
  }
  .post-exit-active {
    transform: translateX(-350px);
    transition: all 500ms ease-in;
  }
  
  .page__wrapper{
    margin: 30px 0;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
  }

  .page{
    border: 1px solid teal;
    padding: 6px; margin: 2px;
    cursor: pointer;
  }
  .page__current{
    border: 2px solid orangered;
    font-weight: bold;
  }

  .navbar{
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background: lightgray;
  }

  .navbar__links{
    margin-left: auto;
    padding: 0 30px;
  }

  .navbar__links_a{
    margin: 0 15px;
    text-decoration: none;
    color: teal;
    transition: 0.3s all linear;
    font-size: 18px;
    font-weight: bold;
    opacity: 0.8;
  }
  .navbar__links_a:hover{
    opacity: 1;

  }